.daily-summary-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}

.daily-summary-header h2 {
  margin: 0;
  font-size: 1.2em;
}

.date-picker {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
}

.daily-summary-grid-old {
  display: grid;
  grid-template-rows: auto auto; /* Two rows */
  grid-template-columns: repeat(5, 1fr); /* Five columns */
  gap: 10px;
  margin-bottom: 20px;
}

.daily-summary-grid {
  display: flex;
  flex-direction: column; /* Stack rows vertically */
  border-radius: 5px;
  overflow: hidden; /* Contain borders */
  margin-bottom: 20px; /* Space before food list */
}

.grid-row-old {
  display: contents; /* Allow children to participate in the grid */
}

.grid-row {
  display: flex;
}
.grid-row:last-child {
  border-bottom: none; /* No line after the last row */
}

.grid-cell-old {
  padding: 10px;
  text-align: center;
  border-bottom: 1px solid #eee;
  font-size: 0.9em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid-cell-old:nth-child(n+6) {
  font-weight: bold;
  font-size: 1.1em;
}

.grid-cell {
  flex: 1; /* Each cell takes equal width */
  padding: 10px 3px; /* Adjust padding as needed */
  text-align: center;
  border-right: 1px solid #eee; /* Vertical separator */
  display: flex; /* Use flex for alignment within cell */
  justify-content: center; /* Center content horizontally */
  align-items: center; /* Center content vertically */
  min-width: 0; /* Prevent flex items from overflowing */
}

.grid-cell:last-child {
  border-right: none; /* No line after the last cell */
}

/* Style the label row */
.grid-row.labels .grid-cell {
  background-color: #f8f8f8;
  font-size: 0.9em;
}

/* Style the value row */
.grid-row.values .grid-cell {
  font-size: 1em;
}

/* Style the progress bar row */
.grid-row.progress-bars .grid-cell {
  padding-top: 5px; /* Add a little space above the bar */
  padding-bottom: 5px; /* Add a little space below the bar */
  /* Ensure the progress bar component inside fills the cell */
  align-items: stretch; /* Make items stretch vertically if needed */
}

/* Ensure the progress bar itself takes up the available width */
.grid-row.progress-bars .grid-cell .nutrient-progress {
    width: 100%;
    /* padding: 0 5px; /* Optional: add horizontal padding inside the cell */
}