/* src/components/RecordMeal.css (or add to App.css) */
.record-meal-header {
    display: flex;
    justify-content: space-between; /* Pushes title left, date picker right */
    align-items: center; /* Vertically aligns items */
    margin-bottom: 20px; /* Add some space below the header */
  }
  
  /* Optional: Adjust h2 margin if needed */
  .record-meal-header h2 {
    margin: 0; /* Remove default margin if it interferes */
    flex-grow: 1; /* Allow title to take available space */
    margin-right: 15px; /* Add space between title and date picker */
  }
  
  /* Ensure date picker doesn't take too much space */
  .record-meal-header .date-picker {
    flex-shrink: 0; /* Prevent shrinking */
  }
  