/* src/components/NutrientProgressBar.css */

.nutrient-progress {
    margin-bottom: 0;
    font-size: 0.9em;
  }
  
  .nutrient-values {
    margin-bottom: 3px;
    color: #555;
    font-size: 0.8em;
    text-align: center;
  }
  
  .progress-bar-container {
    display: flex;
    height: 6px; 
    width: 100%;
    border-radius: 3px;
    overflow: hidden;
    background-color: #e0e0e0; 
  }
  
  .segment {
    flex: 1;
    height: 100%;
    transition: background-color 0.3s ease;
  }
  
  /* Define the colors for active states */
  .segment.yellow.active {background-color: #facc15;}
  
  .segment.green.active {background-color: #4ade80;}
  
  .segment.red.active {background-color: #f87171;}
  
  .segment.yellow {order: 1;} 
  .segment.green {order: 2;} 
  .segment.red {order: 3;}
  .segment:not(.active) {background-color: transparent;}
  